@mixin on-circle($item-count, $circle-size, $item-size, $fadeIn) {  
  
    > * {
      display: block;
      position: absolute;
      margin: -($item-size / 2);
      width:  $item-size;
      height: $item-size;
    
      $angle: (360 / $item-count);
      $rot: 0;
  
      @for $i from 1 through $item-count {
  
        &:nth-of-type(#{$i}) {
          $degreeR1: $rot * 1deg;
          $degreeR2: $rot * -1deg;
          transform: rotate($degreeR1) translate($circle-size / 2) rotate(45deg); 

          .text{
            white-space: nowrap;
            display: flex;
            align-items: center;
            padding: 8px;
            margin-left: 16px;
            transform: rotate(-$rot * 1deg - 5deg);
            &:active{
              //box-shadow: 0 2px 15px rgb(255 0 130 / 20%);
              border-radius: 45px;
              box-shadow: 0 1px 12px blue;
            }

          }

          .material-symbols-sharp {
            font-size: 55px;
            rotate: -35deg; 
          }

          .options-container {
            rotate: 321deg;
          }  
          
        }
  
        $rot: $rot + $angle;
      }
  
    }
 }


.component > * {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

