$color:grey;
  
  
@keyframes ripple {
    0% {
      box-shadow: 0 0 0 0 rgba($color, 0.3),
        0 0 0 1px rgba($color, 0.3),
        0 0 0 3px rgba($color, 0.3),
        0 0 0 5px rgba($color, 0.3);
    }
    100% {
      box-shadow: 0 0 0 0 rgba($color, 0.3),
        0 0 0 4px rgba($color, 0.3),
        0 0 0 20px rgba($color, 0),
        0 0 0 30px rgba($color, 0);
    }
  }
  
